export default Component;

function getTable(h, {title, type, items}, id) {
  return (
    <div class="example-box" key={id}>
      <h4 class="ui header">{title}</h4>
       <a id={id} class="anchor"></a>
        {type === 'props' &&
          <table class="uk-table uk-table-striped uk-table-small">
            <thead>
                <tr>
                    <th>Property</th>
                    <th>Description</th>
                    <th>Type</th>
                    <th>Options</th>
                    <th>Default</th>
                </tr>
            </thead>
            <tbody>
                {items.map((item, index) => 
                  <tr key={index}>
                    <td>{item.prop}</td>
                    <td>{item.description}</td>
                    <td>{item.type}</td>
                    <td>{item.accepted}</td>
                    <td>{item.default}</td>
                  </tr>
                )}
            </tbody>
          </table>
        }
        {type === 'events' &&
          <table class="uk-table uk-table-striped uk-table-small">
            <thead>
                <tr>
                    <th>Event name</th>
                    <th>Description</th>
                    <th>Return value</th>
                </tr>
            </thead>
            <tbody>
                {items.map((item, index) => 
                  <tr key={index}>
                    <td>{item.event}</td>
                    <td>{item.description}</td>
                    <td>{item.return}</td>
                  </tr>
                )}
            </tbody>
          </table>
        }
        {type === 'slots' &&
          <table class="uk-table uk-table-striped uk-table-small">
            <thead>
                <tr>
                    <th>Slot name</th>
                    <th>Description</th>
                </tr>
            </thead>
            <tbody>
                {items.map((item, index) => 
                  <tr key={index}>
                    <td>{item.slot}</td>
                    <td>{item.description}</td>
                  </tr>
                )}
            </tbody>
          </table>
        }
    </div>
  )
}




