<template>
  <div>
    <page-row :margin="2">
      <vu-slider :items="images" 
        :width="width" :height="height" 
        :dots="dots" 
        :dotsInside="dotsInside"
        :progress="progress" 
        :buttonsOutside="buttonsOutside"
        :wheel="wheel"
        :wheelReverse="wheelReverse"
        :dotColor="dotColor"
        :dotActiveColor="dotActiveColor"
        :progressColor="progressColor"
        :progressBgColor="progressBgColor"/>
    </page-row>

    <page-row :margin="1">
      <span class="page-row__span">Dots position</span>
      <div class="uk-button-group">
        <button v-for="(type, index) in types" :class="[
          'uk-button',
          'uk-button-smaller',
          dots === type ? 'uk-button-primary' : 'uk-button-default'
        ]" @click="dots = type">{{type}}</button>
      </div>
    </page-row>
    
    <page-row :margin="1">
      <span class="page-row__span">Dots</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[dotsInside === true ? 'uk-button-primary' : 'uk-button-default']" @click="dotsInside = true">Inside</button>
        <button class="uk-button uk-button-smaller" 
          :class="[dotsInside === true ? 'uk-button-default' : 'uk-button-primary']" @click="dotsInside = false">Outside</button>
      </div>
    </page-row>

    <page-row :margin="1">
      <span class="page-row__span">Dot color</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[dotColor === 'lightgrey' ? 'uk-button-primary' : 'uk-button-default']" @click="dotColor = 'lightgrey'">lightgrey</button>
        <button class="uk-button uk-button-smaller" 
          :class="[dotColor === 'white' ? 'uk-button-primary' : 'uk-button-default']" @click="dotColor = 'white'">white</button>
        <button class="uk-button uk-button-smaller" 
          :class="[dotColor === 'lightgreen' ? 'uk-button-primary' : 'uk-button-default']" @click="dotColor = 'lightgreen'">lightgreen</button>
      </div>
    </page-row>

    <page-row :margin="1">
      <span class="page-row__span">Dot active color</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[dotActiveColor === 'grey' ? 'uk-button-primary' : 'uk-button-default']" @click="dotActiveColor = 'grey'">grey</button>
        <button class="uk-button uk-button-smaller" 
          :class="[dotActiveColor === 'white' ? 'uk-button-primary' : 'uk-button-default']" @click="dotActiveColor = 'white'">white</button>
        <button class="uk-button uk-button-smaller" 
          :class="[dotActiveColor === 'green' ? 'uk-button-primary' : 'uk-button-default']" @click="dotActiveColor = 'green'">green</button>
      </div>
    </page-row>
    
    <page-row :margin="1">
      <span class="page-row__span">Buttons</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[buttonsOutside === true ? 'uk-button-default' : 'uk-button-primary']" @click="buttonsOutside = false">Inside</button>
        <button class="uk-button uk-button-smaller" 
          :class="[buttonsOutside === true ? 'uk-button-primary' : 'uk-button-default']" @click="buttonsOutside = true">Outside</button>
      </div>
    </page-row>

    <page-row :margin="1">
      <span class="page-row__span">Progress</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[progress === '' ? 'uk-button-primary' : 'uk-button-default']" @click="progress = ''">no</button>
        <button class="uk-button uk-button-smaller" 
          :class="[progress === 'top' ? 'uk-button-primary' : 'uk-button-default']" @click="progress = 'top'">top</button>
        <button class="uk-button uk-button-smaller" 
          :class="[progress === 'bottom' ? 'uk-button-primary' : 'uk-button-default']" @click="progress = 'bottom'">bottom</button>
      </div>
    </page-row>

    <page-row :margin="1">
      <span class="page-row__span">Progress color</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[progressColor === 'lightgrey' ? 'uk-button-primary' : 'uk-button-default']" @click="progressColor = 'lightgrey'">lightgrey</button>
        <button class="uk-button uk-button-smaller" 
          :class="[progressColor === 'grey' ? 'uk-button-primary' : 'uk-button-default']" @click="progressColor = 'grey'">grey</button>
        <button class="uk-button uk-button-smaller" 
          :class="[progressColor === 'green' ? 'uk-button-primary' : 'uk-button-default']" @click="progressColor = 'green'">green</button>
      </div>
    </page-row>

    <page-row :margin="1">
      <span class="page-row__span">Progress background color</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[progressBgColor === 'transparent' ? 'uk-button-primary' : 'uk-button-default']" @click="progressBgColor = 'transparent'">transparent</button>
        <button class="uk-button uk-button-smaller" 
          :class="[progressBgColor === 'lightgrey' ? 'uk-button-primary' : 'uk-button-default']" @click="progressBgColor = 'lightgrey'">lightgrey</button>
        <button class="uk-button uk-button-smaller" 
          :class="[progressBgColor === 'lightgreen' ? 'uk-button-primary' : 'uk-button-default']" @click="progressBgColor = 'lightgreen'">lightgreen</button>
      </div>
    </page-row>
    
   
    <page-row :margin="1">
      <span class="page-row__span">Slider height</span>
      <div class="uk-button-group">
         <button class="uk-button uk-button-smaller" 
          :class="[height === 200 ? 'uk-button-primary' : 'uk-button-default']" @click="height = 200">200px</button>
        <button class="uk-button uk-button-smaller" 
          :class="[height === 300 ? 'uk-button-primary' : 'uk-button-default']" @click="height =300">300px</button>
        <button class="uk-button uk-button-smaller" 
          :class="[height === 500 ? 'uk-button-primary' : 'uk-button-default']" @click="height = 500">500px</button>
      </div>
    </page-row>
    
    <page-row :margin="1">
      <span class="page-row__span">Slider width</span>
      <div class="uk-button-group">
         <button class="uk-button uk-button-small" 
          :class="[width === 500 ? 'uk-button-primary' : 'uk-button-default']" @click="width = 500">500px</button>
        <button class="uk-button uk-button-smaller" 
          :class="[width === 600 ? 'uk-button-primary' : 'uk-button-default']" @click="width = 600">600px</button>
        <button class="uk-button uk-button-smaller" 
          :class="[width === 700 ? 'uk-button-primary' : 'uk-button-default']" @click="width = 700">700px</button>
        <button class="uk-button uk-button-smaller" 
          :class="[width === '100%' ? 'uk-button-primary' : 'uk-button-default']" @click="width = '100%'">100%</button>
      </div>
    </page-row>

    <page-row>
      <span class="page-row__span">Wheel control</span>
      <div class="uk-button-group">
        <button class="uk-button uk-button-smaller" 
          :class="[wheel === false && wheelReverse === false ? 'uk-button-primary' : 'uk-button-default']" 
          @click="wheel = false; wheelReverse = false">Off</button>
        <button class="uk-button uk-button-smaller" 
          :class="[wheel === true ? 'uk-button-primary' : 'uk-button-default']" @click="wheel = true; wheelReverse = false">On</button>
        <button class="uk-button uk-button-smaller" 
          :class="[wheelReverse === true ? 'uk-button-primary' : 'uk-button-default']" @click="wheel = false; wheelReverse = true">Reverse</button>
      </div>
    </page-row>

  </div>
</template>

<script>
import { getImages } from '@/mock/images'
export default {
  name: 'play',
  data() {
    return {
      types: ['top', 'bottom', 'top-left', 'top-right', 'bottom-left', 'bottom-right'],
      dots: 'top',
      height: 200,
      width: '100%',
      dotsInside: true,
      progress: '',
      buttonsOutside: false,
      wheel: false,
      wheelReverse: false,
      dotColor: 'lightgrey',
      dotActiveColor: 'grey',
      progressColor: 'lightgrey',
      progressBgColor: 'transparent',
      images: getImages(15)
    }
  }
};
</script>


