<template lang="html">
  <div class="page">
    <div class="page-sidebar page-sidebar_right">
        <ul class="uk-nav uk-nav-default">
          <li>
            <div class="sidebar__title">
              <router-link tag="h5" class="sidebar__header" to="/"><b>Vue universal slider</b></router-link>
              <a href="https://github.com/vuNemesis/vue-universal-slider" uk-icon="github" class="sidebar__github-icon" target="_blank"></a>
            </div>
           </li>
         <li class="uk-nav-divider"></li>
        </ul>

        <ul class="uk-nav uk-nav-default nav-list-padding">
            <li class="uk-parent">
                <li class="nav-header"><router-link to="/">Examples</router-link></li>
                <ul class="uk-nav-sub">
                  <li v-for="({link, header}, index) in links"><router-link :to="link">{{header}}</router-link></li>
                </ul>
            </li>
            <li class="uk-nav-divider"></li>
        </ul>

        <ul class="uk-nav uk-nav-default nav-list-padding">
            <li class="uk-parent">
                <li class="nav-header">Props & Events & Slots</li>
                <ul class="uk-nav-sub">
                  <li v-for="({link, header}, index) in propsLinks"><router-link :to="link">{{header}}</router-link></li>
                </ul>
            </li>
        </ul>

        <ul class="uk-nav uk-nav-default nav-list-padding">
            <li class="uk-parent" style="margin-top: 15px">
                <li class="nav-header"><router-link style="font-size: 1em" to="/play">Play it!</router-link></li>
            </li>
        </ul>

    </div>
    
   <!--  <div class="page-header">
      <slot name="header"></slot>
    </div> -->
   <div class="page-content">
    <router-view/>
  </div>
  </div>
</template>

<script>
export default {
  name: 'page-content',
  data() {
    return {
      links: [],
      propsLinks: []
    }
  },
  methods: {
    setLinks(links) {
      this.links = links
    },
    setPropsLinks(links) {
      this.propsLinks = links
    }
  }
};
</script>

<style lang="scss">
.page {
  padding-left: 280px;
  &-header {
    background-color: aqua;
    padding: 2em;
    border-bottom: 1px solid rgba(34, 36, 38, 0.15);
  }
  &-content {
    padding: 2.5em;
  }
}
</style>


