import { html } from 'js-beautify'
const Component = {
  name: 'example-box',
  props: ['example'],
  data() {
    return {
      activeTab: 'code',
      hiddenVisible: true,
      link: '',
      exampleInfo: this.example.__example
    }
  },
  methods: {
    renderExample(h, cmp) {
      let code = h(cmp)
      return <div ref="compiled">{code}</div>
    },
    toggleHiddenVisible(tab) {
      if(tab) this.activeTab = tab
      this.hiddenVisible = !this.hiddenVisible
      this.$emit('show-code', this.hiddenVisible)
    },
    formatHTML(code) {
      const preFormattedHTML = code.replace(/><(?!\/i|\/label|\/span|option)/g, '>\n<');
      return html(preFormattedHTML, {
        indent_size: 2,
        indent_char: ' ',
        wrap_attributes: 'auto',
        wrap_attributes_indent_size: 2,
        end_with_newline: false,
      })
    },
    getCode() {
      let code = this.exampleInfo.template + '\n\n<script>' + this.exampleInfo.script + '<' + '/script>'
      return this.formatHTML(code)
    },
    getHTML() {
      let compiledHTML = this.$refs.compiled.outerHTML
      return this.formatHTML(compiledHTML)
    },
    setActiveTab(tab) {
      this.activeTab = tab
    }
  },
  beforeMount() {
    this.link = this.exampleInfo.key
    this.$emit('push-header', this.exampleInfo.info.title, this.link)
  },
  directives: {
    html: function(el, binding) {
      el.innerHTML = binding.value
    }
  },
  render(h) {
    const { exampleInfo, example } = this
    return (
        <div class={['example-box', !this.hiddenVisible && 'active']}>
         <h4>
          {exampleInfo.info.title}
          {exampleInfo.info.description && <div class="sub-header" v-html={example.__example.info.description}></div>}
         </h4>
          <a id={this.link} class="anchor"></a>
              <div class="content-example">
                {this.renderExample(h, example)}
              </div>
              <div class="content-buttons">
                {this.hiddenVisible && <fai icon="code" class="link" on-click={() => this.toggleHiddenVisible('code')}/>}
                {this.hiddenVisible && <fai icon={['fab', 'html5']} class="link" on-click={() => this.toggleHiddenVisible('result')}/>}
                {!this.hiddenVisible && <fai icon={['far', 'times-circle']} class="link" on-click={this.toggleHiddenVisible}/>}
              </div>

            {!this.hiddenVisible && (
              <div class="content-code">
                 <ul class="ui-tab">
                   <li class={[this.activeTab === 'code' && 'ui-tab_active']}><a on-click={this.setActiveTab.bind(this, 'code')}>Code</a></li>
                   <li class={[this.activeTab === 'result' && 'ui-tab_active']}><a on-click={this.setActiveTab.bind(this, 'result')}>Result HTML</a></li>
                   {exampleInfo.info.info && 
                      <li class={[this.activeTab === 'info' && 'ui-tab_active']}>
                        <a on-click={this.setActiveTab.bind(this, 'info')}>Info</a>
                      </li>}
                 </ul>
                <div>
                  {this.activeTab === 'code' && (
                    <pre v-highlightjs key="code"><code class="xml" style={{'font-size': '0.9em', padding: '1em'}}>{this.getCode()}</code></pre>
                  )}
                  {this.activeTab === 'result' && (
                    <pre v-highlightjs key="result"><code class="html" style={{'font-size': '0.9em', padding: '1em'}}>{this.getHTML()}</code></pre>
                  )}
                  {exampleInfo.info.info && this.activeTab === 'info' && (
                    <div v-html={exampleInfo.info.info}/>
                  )}
                </div>
              </div>
              
              // <div class="content-code">
              //   <ui-tabs v-model={this.activeTab} type="pointing">
              //     <ui-tabs-panel name="code" label="Code">
              //       <pre v-highlightjs><code class="html" style={{'font-size': '0.8em'}}>{this.getCode()}</code></pre>
              //     </ui-tabs-panel>

              //     <ui-tabs-panel name="result" label="Result HTML">
              //       <pre v-highlightjs><code class="html" style={{'font-size': '0.8em'}}>{this.getHTML()}</code></pre>
              //     </ui-tabs-panel>
              //   </ui-tabs>
              // </div>
            )}
          {!this.hiddenVisible && (
            <div on-click={() => this.toggleHiddenVisible()} class="content-code-hide">Hide code</div>
          )}
        </div>
    )
  }
};

export default Component;



