export default {
  header: 'Vue-universal-slider',
  subHeader: 'A group of options for multiple choices.',
  meta: [
    {
      title: 'Slider props',
      type: 'props',
      items: [
        {
          prop: 'items',
          default: "[]",
          type: 'Array',
          accepted: '',
          description: 'image array'
        },
        {
          prop: 'height',
          default: '100px',
          type: 'Number',
          accepted: '',
          description: 'slider height'
        },
        {
          prop: "width",
          default: "100%",
          type: "String/Number",
          accepted: "",
          description: "slider width"
        },
        {
          prop: "dots",
          default: "''",
          type: "String",
          accepted: "'top', 'bottom'",
          description: "whether there are dots at the Slider"
        },
        {
          prop: "dots-inside",
          default: "false",
          type: "Boolean",
          accepted: "",
          description: ""
        },
        {
          prop: "progress",
          default: "''",
          type: "String",
          accepted: "'top', 'bottom'",
          description: "whether there are progress-bar at the Slider"
        },
        {
          prop: "buttons-outside",
          default: "false",
          type: "Boolean",
          accepted: "",
          description: ""
        },
        {
          prop: "wheel",
          default: "false",
          type: "Boolean",
          accepted: "",
          description: ""
        },
        {
          prop: "wheel-reverse",
          default: "false",
          type: "Boolean",
          accepted: "",
          description: ""
        }
      ]
    },
    {
      title: 'Slider events',
      type: 'events',
      items: [
        {
          event: 'on-change',
          description: 'Emitted when the state of selection is changed',
          return: 'the checked value'
        }
      ]
    },
     {
      title: 'Slider slots',
      type: 'slots',
      items: [
        {
          slot: 'scoped-slot',
          description: 'Emitted when the state of selection is changed'
        }
      ]
    }
  ]
}


